import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import reportWebVitals from './reportWebVitals';
import GlobalStyle from './GlobalStyle';
import { HashRouter } from 'react-router-dom';


ReactDOM.render(

  <React.StrictMode>
  <HashRouter basename='/'>
    <GlobalStyle/>
        
    <App />
  </HashRouter>
</React.StrictMode>,
  document.getElementById('root')
);



